"use client"

import { useEffect, useRef, useState } from "react"
import { Building2, Globe2, ShoppingBag } from "lucide-react"

export function AITeamSection() {
  const sectionRef = useRef<HTMLElement>(null)
  const [isVisible, setIsVisible] = useState(false)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true)
        }
      },
      {
        threshold: 0.1,
        rootMargin: "0px 0px -100px 0px",
      },
    )

    if (sectionRef.current) {
      observer.observe(sectionRef.current)
    }

    return () => {
      if (sectionRef.current) {
        observer.unobserve(sectionRef.current)
      }
    }
  }, [])

  const projects = [
    {
      title: "B2B SaaS · Website & CRO",
      result: "+42% lead demo trong 60 ngày",
      desc: "Tái thiết kế landing chính, tối ưu CTA theo hành trình khách hàng, tích hợp HubSpot và tracking đầy đủ.",
      icon: Building2,
      tag: "SaaS",
    },
    {
      title: "Chuỗi bán lẻ · SEO & nội dung",
      result: "+68% organic traffic, +35% chuyển đổi form",
      desc: "Xây lại cấu trúc site, triển khai schema, tối ưu Core Web Vitals và lịch nội dung 3 tháng.",
      icon: ShoppingBag,
      tag: "Retail",
    },
    {
      title: "Du lịch/Resort · Trải nghiệm đặt phòng",
      result: "-30% bounce, +50% booking trực tiếp",
      desc: "Thiết kế lại UI/UX, thêm gói ưu đãi động, kết nối CRM và tự động nhắc lịch qua email/WA.",
      icon: Globe2,
      tag: "Hospitality",
    },
  ]

  return (
    <section id="projects" ref={sectionRef} className="relative z-10">
      <div className="bg-white rounded-b-[3rem] pt-16 sm:pt-24 pb-16 sm:pb-24 px-4 relative overflow-hidden">
        <div className="max-w-7xl mx-auto">
          <div className="text-center mb-14">
            <div
              className={`inline-flex items-center gap-2 bg-blue-50 border border-blue-100 text-blue-700 px-4 py-2 rounded-full text-sm font-medium mb-6 transition-all duration-1000 ${
                isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8"
              }`}
            >
              Dự án
            </div>

            <h2
              className={`text-4xl md:text-5xl font-bold text-slate-900 mb-4 transition-all duration-1000 delay-200 ${
                isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8"
              }`}
            >
              Case study tiêu biểu
            </h2>

            <p
              className={`text-lg text-slate-600 max-w-2xl mx-auto transition-all duration-1000 delay-400 ${
                isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8"
              }`}
            >
              Những dự án gần đây chúng tôi triển khai, tập trung vào kết quả đo lường rõ ràng và thời gian thực thi nhanh.
            </p>
          </div>

          <div
            className={`grid gap-6 md:grid-cols-3 transition-all duration-1000 ${
              isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8"
            }`}
          >
            {projects.map((project, index) => (
              <div
                key={project.title}
                className="rounded-2xl border border-slate-200 bg-white shadow-lg hover:shadow-2xl transition-all duration-300 hover:-translate-y-2 p-6 flex flex-col"
                style={{
                  transitionDelay: isVisible ? `${150 + index * 100}ms` : "0ms",
                }}
              >
                <div className="flex items-center gap-3 mb-4">
                  <div className="w-12 h-12 rounded-2xl bg-blue-50 text-blue-700 flex items-center justify-center">
                    <project.icon className="w-6 h-6" />
                  </div>
                  <div>
                    <p className="text-sm text-blue-700 font-medium">{project.tag}</p>
                    <h3 className="text-lg font-semibold text-slate-900">{project.title}</h3>
                  </div>
                </div>
                <p className="text-sm text-blue-700 font-semibold mb-2">{project.result}</p>
                <p className="text-slate-600 leading-relaxed flex-1">{project.desc}</p>
                <div className="mt-4 h-px bg-slate-100" />
                <div className="mt-3 text-sm text-slate-500">Thời gian triển khai: 4-6 tuần</div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  )
}
